#products {
  h2 {
    @include h2;
    margin-bottom: 53px;
    color: $color-black-dark;

    @include viewport--md {
      margin-bottom: 34px;
      width: 216px;
      font-size: 32px;
      line-height: 38px;
    }
  }
}

.products {
  padding: 51px 5.5% 38px;
  background-color: $color-white-dark;

  @include viewport--md {
    padding: 31px 24px 13px 21px;
    margin: 0 -24px;
  }
}

.products__list {
  @include list-style-reset;
}

.products-block {
  margin-bottom: 24px;
  display: flex;
  justify-content: space-between;
  background-color: $color-default-white;
  padding-bottom: 22px;
  border-radius: 16px;

  &--rag {
    .products-block__text {
      padding-top: 68px;

      @include viewport--md {
        padding-top: 0;
      }
    }

    .products-block__title {
      margin-bottom: 5px;

      @include viewport--md {
        padding: 0 24px 34px;
      }
    }

    .products-block__photo {
      height: 356px;

      @include viewport--md {
        height: 208px;
      }
    }
  }

  @include viewport--md {
    margin-bottom: 17px;
    flex-wrap: wrap;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.products-block__picture {
  display: none;

  &:nth-child(1) {
    display: block;
  }

  img {
    object-fit: cover;
  }

  @include viewport--md {
    img {
      width: 312px;
      max-height: 252px;
    }
  }
}

.products-block__title {
  display: inline;
  margin: 0 0 43px;
  padding-right: 17%;
  // min-height: 121px;
  font-size: 21px;
  line-height: 35px;
  letter-spacing: -0.2px;

  @include viewport--md {
    margin: 0;
    padding: 0 7.6%;
    align-items: center;
    font-size: 18px;
    line-height: 27px;
  }

  @include viewport--sm {
    padding: 8px 24px 11px;
  }

  &--small {
    padding-right: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    text-align: start;
    letter-spacing: -0.2px;
    min-height: 71px;

    @include viewport--sm {
      padding: 0 0 24px;
    }
  }
}

.product-block__title-inscription {
  font-weight: bold;

  &--orange {
    color: $color-orange;
  }
}

.products-block__photo {
  margin: 0 auto;
  width: 534px;
  height: 432px;
  max-width: 53%;

  @include viewport--md {
    width: 312px;
    height: 252px;
    max-width: none;
  }
}

.products-block__text {
  display: flex;
  flex-direction: column;
  padding-top: 71px;
  width: 47.2%;

  @include viewport--md {
    padding: 0 0 10px;
    width: 100%;
  }
}

.products-block__color {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 45px;
  padding: 14px 0;

  ul {
    @include list-style-reset;
    display: flex;
  }

  p {
    margin: 15px 47px 15px 0;
    opacity: 0.5;
    font-size: 16px;
    line-height: 21px;
    text-transform: uppercase;
  }

  li {
    margin-right: 17px;

    &:last-child {
      margin-right: 0;
    }
  }

  label {
    @include on-hover;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    width: 32px;
    height: 32px;
    border-radius: 50%;

    input {
      display: none;
    }

    input:checked ~ span {
      display: block;
    }

    span {
      display: none;
    }

    .products__mark-color-white svg path {
      fill: $color-violet;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: $color-grey-dark-transparent-20;
  }

  @include viewport--md {
    margin: 0;
    padding: 18px 7.2%;
  }

  @include viewport--sm {
    flex-wrap: wrap;
  }
}

.products-block__buy {
  position: relative;
  padding-right: calc(60% - 222px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

  button {
    // margin-bottom: 50px;
    padding: 12px 8px;
    display: inline-block;
    min-width: 194px;

    svg {
      margin-left: 6px;
      margin-right: 0;
      vertical-align: text-bottom;

      @include viewport--md {
        width: 20px;
        height: 20px;
      }
    }

    @include viewport--md {
      margin-right: 0;
      margin-bottom: 9px;
      min-width: 125px;
      font-size: 16px;
      line-height: 24px;
    }
  }

  @include price-block;

  .price-block {
    display: flex;
    flex-direction: column;
    // margin: 3px 0 15px;
  }

  .price-block__item {
    min-width: 172px;
    font-size: 32px;
    color: $color-black-dark;

    @include viewport--md {
      min-width: 128px;
      font-size: 21px;
    }
  }

  .price-block__item--old {
    color: $color-orange;
    font-size: 21px;
    line-height: 28px;

    @include viewport--md {
      font-size: 18px;
      font-weight: 400;
      line-height: 18px;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: -41px;
    right: 0;
    width: 100%;
    height: 1px;
    background-color: $color-grey-dark-transparent-20;

    @include viewport--md {
      top: -20px;
    }
  }

  @include viewport--md {
    margin: 16px 0 0;
    padding: 0 7.6%;
  }
}
