.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background-color: rgba($color-default-black, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  transition: transform $modal-transition, visibility $modal-transition, opacity $modal-transition;

  &::-webkit-scrollbar {
    display: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }

  &--feedback {
    & .container {
      background-image: url("../img/bg/bg-modal-feedback-desktop@1x.png");
      background-position: right calc(50% - 83px) bottom 0;
      background-size: 620px 765px;
      background-repeat: no-repeat;

      @include retina {
        background-image: url("../img/bg/bg-modal-feedback-desktop@2x.png");
      }

      @include viewport--md {
        background-image: url("../img/bg/bg-modal-feedback-mobile@1x.png");
        background-position: right 0 top -4px;
        background-size: 154px 356px;
        background-repeat: no-repeat;

        @include retina {
          background-image: url("../img/bg/bg-modal-feedback-mobile@2x.png");
        }
      }
    }

    & .modal__inner-content {
      padding-bottom: 134px;

      @include viewport--md {
        padding-bottom: 18px;

        & h2 {
          max-width: 80%;
        }

        & h2 + p {
          max-width: 80%;
        }
      }
    }

    & button[type="submit"] svg {
      fill: $color-default-white;

      @include viewport--md {
        max-width: 24px;
      }
    }
  }

  &--order {
    & .container {
      background-image: url("../img/bg/bg-modal-order-desktop@1x.png");
      background-position: right 0 top -96px;
      background-size: 670px 1312px;
      background-repeat: no-repeat;

      @include retina {
        background-image: url("../img/bg/bg-modal-order-desktop@2x.png");
      }

      @include viewport--md {
        background-image: url("../img/bg/bg-modal-order-mobile@1x.png");
        background-position: right 0 top -10px;
        background-size: 182px 392px;
        background-repeat: no-repeat;

        @include retina {
          background-image: url("../img/bg/bg-modal-order-mobile@2x.png");
        }
      }

      @include viewport--sm {
        padding: 0 calc(35% - 102px);
      }
    }

    & .modal__inner-content {
      max-width: calc(10% + 641px);
      padding-bottom: 78px;

      @include viewport--md {
        max-width: 100%;
        padding-bottom: 20px;

        & h2 {
          max-width: 80%;
        }

        & .btn {
          min-width: 100%;
        }
      }
    }

    & .modal__form {
      max-width: 81%;
      margin-bottom: 33px;

      @include viewport--sm {
        width: 100%;
        max-width: none;
        margin-bottom: 23px;
      }
    }

    & .form-input {
      flex-grow: 1;
      margin-bottom: 30px;

      @include viewport--sm {
        margin-bottom: 16px;
      }

      &--name,
      &--surname {
        max-width: 48%;

        @include viewport--sm {
          width: 100%;
          max-width: none;
        }
      }

      &--phone {
        width: 100%;
      }
    }

    & h2 + p {
      max-width: 80%;
      margin: 0 0 29px;

      @include viewport--md {
        margin: 0 0 24px;
      }
    }

    & button svg {
      @include viewport--md {
        max-width: 24px;
      }
    }

    & .modal__privacy p {
      max-width: 81%;
      margin: 0;

      @include viewport--sm {
        max-width: 100%;
      }
    }
  }

  &--success {
    & .modal__content {
      min-height: 875px;

      @include viewport--md {
        min-height: 641px;
      }
    }

    & .container {
      background-image: url("../img/bg/bg-modal-success-desktop@1x.png");
      background-position: right calc(50% - 184px) top 49px;
      background-size: 241px 283px;
      background-repeat: no-repeat;

      @include retina {
        background-image: url("../img/bg/bg-modal-success-desktop@2x.png");
      }

      @include viewport--md {
        background-image: url("../img/bg/bg-modal-success-mobile@1x.png");
        background-position: right 0 top 31px;
        background-size: 57px 182px;
        background-repeat: no-repeat;

        @include retina {
          background-image: url("../img/bg/bg-modal-success-mobile@2x.png");
        }
      }
    }

    & h2 span {
      display: block;
    }

    & .modal__inner-content {
      max-width: 537px;
      padding-bottom: 233px;

      @include viewport--md {
        max-width: 480px;
        padding-bottom: 197px;

        & h2 + p {
          max-width: 85%;
        }
      }
    }

    & .btn--color span {
      @include viewport--md {
        margin: 0 14px 0 0;
      }
    }
  }

  & header .container {
    background-image: none;
  }

  .modal__product-block {
    flex-wrap: nowrap;
    border: 1px solid $color-periwinkle;
    border-radius: 10px;
    justify-content: flex-start;

    box-shadow: $shadow-light;

    overflow: hidden;

    @include viewport--sm {
      flex-wrap: wrap;
    }

    & .products-block__photo {
      flex-shrink: 0;
      width: auto;
      max-width: calc(80% - 210px);
      height: auto;
      margin: 0 calc(30% - 153px) 0 0;
      padding-left: calc(30% - 146px);

      @include viewport--md {
        width: 40%;
        max-width: none;
        margin: 0 5% 0 0;
        padding-left: 10px;

        & img {
          width: auto;
          max-height: none;
          margin: 0 auto;
        }
      }

      @include viewport--sm {
        width: 100%;

        & img {
          width: auto;
          max-height: 155px;
          margin: 0 auto;
        }
      }
    }

    & .products-block__text {
      flex-grow: 1;
      width: auto;
      padding: 25px 0 5px;

      @include viewport--sm {
        padding: 25px 0 0;
      }

      & p {
        margin: 0;
        padding-right: 10px;

        font-size: 18px;
        line-height: 27px;

        @include viewport--sm {
          padding: 0 10px 0 23px;
        }
      }

      & .modal__product-promo {
        font-weight: bold;
        color: $color-orange;

        &--second-inscription {
          color: $color-black-light;
        }
      }

      & .modal__product-block-color {
        justify-content: space-between;
        padding: 24px 40px 24px 0;

        border-bottom: 1px solid $color-periwinkle;

        @include viewport--md {
          justify-content: flex-start;
        }

        @include viewport--sm {
          padding: 24px;
        }

        & p {
          margin: 0 10px 0 0;
          padding: 0;

          font-size: 16px;
          line-height: 22px;
          color: $color-black-light;

          opacity: 0.5;

          @include viewport--md {
            margin: 0 43px 0 0;
          }
        }

        & ul {
          flex-wrap: wrap;
        }

        &::before {
          content: none;
        }
      }

      & .modal__product-mark-color {
        &-white {
          & svg path {
            fill: $color-violet;
          }
        }
      }

      & .products-block__buy {
        margin: 0;
        padding: 0 15px 0 0;

        &::before {
          content: none;
        }

        & .products-block__price {
          display: block;
          margin: 0;
          padding: 15px 0;

          @include viewport--sm {
            padding: 24px 10px 24px 21px;
          }

          & .price-block__item {
            margin-right: 15px;

            font-size: 27px;
            line-height: 40px;

            @include viewport--sm {
              font-size: 21px;
              line-height: 31px;
            }

            &--old {
              font-size: 21px;
              line-height: 48px;
              font-weight: 800;

              @include viewport--sm {
                font-family: $font-family--main;
                font-size: 18px;
                line-height: 27px;
                font-weight: 500;
              }
            }
          }
        }
      }
    }
  }
}

.modal__wrapper {
  position: relative;

  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  cursor: pointer;
}

.modal__content {
  position: relative;

  width: 100%;
  max-width: $max-modal-desktop-width;

  background-color: $color-default-white;
}

.modal__form {
  max-width: 352px;
}

.modal__form-personal {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  min-width: 100%;
  margin: 0 0 13px;

  @include viewport--sm {
    margin-bottom: 15px;
  }
}

.modal__form h3 {
  margin: 0 0 23px;

  font-family: $font-family--second;
  font-weight: 800;
  font-size: 24px;
  line-height: 36px;
  color: $color-black-dark;

  @include viewport--sm {
    margin: 0 0 21px;
  }
}

.modal .btn {
  & svg {
    margin: 0 10px 0 0;
  }
}

.modal__form button {
  margin: 24px 0 0;
}

.modal__close-btn {
  @include center-xy-by-flex-row;
  @include on-hover;

  @include viewport--md {
    & svg {
      max-width: 16px;
      max-height: 16px;
    }
  }
}

.modal__inner-content {
  padding: 68px 0;
  max-width: 500px;

  @include viewport--md {
    padding: 29px 0;
  }
}

.modal__inner-content h2 {
  margin: 0 0 32px;

  font-family: $font-family--second;
  font-weight: 800;
  font-size: 56px;
  line-height: 72px;
  letter-spacing: -1px;
  color: $color-black-dark;

  @include viewport--sm {
    margin: 0 0 22px;

    font-size: 32px;
    line-height: 38px;
  }
}

.modal__inner-content p {
  margin: 0 0 40px;

  font-size: 21px;
  line-height: 35px;
  letter-spacing: -0.2px;

  @include viewport--md {
    margin: 0 0 24px;

    font-size: 18px;
    line-height: 27px;
  }
}

.modal__inner-content .btn {
  min-height: 72px;
  min-width: 328px;

  @include viewport--md {
    min-height: auto;
  }
}

.modal__privacy p {
  margin: 25px 0 0;
  max-width: calc(50% + 155px);

  font-size: 14px;
  line-height: 19px;
  letter-spacing: -0.2px;
  color: $color-fossil;
}

.modal__privacy a {
  color: $color-fossil;
  text-decoration: underline;

  transition: opacity $transition;

  &:hover {
    opacity: $opacity-hover;
  }
}

.modal__form-order {
  margin: 0 0 43px;

  @include viewport--sm {
    margin: 0 0 33px;
  }
}

.modal__form-order-item {
  display: flex;
  flex-wrap: wrap;
  padding: 8px 26px 16px 23px;

  background-color: $color-default-white;
  border: 1px solid $color-periwinkle;
  box-shadow: $shadow-periwinkle-25;
  border-radius: 10px;
}

.modal__form-order-image {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: 80px;
}

.modal__form-order-item p {
  width: 100%;
  margin: 0;

  font-family: $font-family--second;
  font-weight: 800;
  font-size: 21px;
  line-height: 31px;
  color: $color-black-dark;
}

.modal__form-order-description {
  display: flex;
  margin: 0 0 7px;
}

.modal__form-order-description picture {
  flex-shrink: 0;
}

.modal__form-order-description p {
  margin: 0 0 0 10px;
  padding: 8px 0;

  font-family: $font-family--main;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $color-black-light;
  letter-spacing: normal;
}

.modal__form-address {
  margin-bottom: 44px;

  @include viewport--sm {
    margin-bottom: 40px;
  }

  & .form-input {
    display: flex;
    margin-bottom: 32px;

    @include viewport--sm {
      margin-bottom: 15px;
    }

    &--hidden {
      display: none;
    }
  }

  & .modal__address-house {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-gap: 2%;

    @include viewport--sm {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-bottom: 9px;

      & .form-input {
        margin-bottom: 17px;

        &--house {
          width: 40%;
          max-width: 48%;
        }

        &--bulk {
          width: 40%;
          max-width: 48%;
        }

        &--flat {
          width: 100%;
        }
      }
    }
  }

  & textarea {
    min-height: 142px;
    width: 100%;
    padding: 26px 22px;

    font-weight: 500;
    color: $color-black-dark;

    border: 1px solid $color-periwinkle;
    border-radius: 10px;
    box-shadow: $shadow-light;

    outline: none;

    @include viewport--sm {
      min-height: 160px;
      padding: 21px 17px;
    }

    &::placeholder {
      font-weight: 400;
      color: $color-black-light;
    }
  }

  & .modal__address-nonstandard {
    margin-bottom: 27px;

    @include viewport--sm {
      margin-bottom: 23px;
    }

    & .form-input {
      margin-bottom: 19px;
    }
  }

  & .modal__address-nonstandard-textarea {
    margin-bottom: 23px;

    &--hidden {
      border-top: 1px solid $color-periwinkle;

      & textarea {
        display: none;
      }
    }
  }
}

.modal__contacts {
  padding: 4px 0 0;
  margin: 0 0 47px;

  @include viewport--md {
    padding: 0;
    margin: 0 0 33px;
  }
}

.modal__contacts p {
  margin: 0;
}

.modal__contacts a {
  @include on-hover;
  display: flex;
  align-items: center;

  font-family: $font-family--second;
  font-weight: 800;
  font-size: 27px;
  line-height: 34px;
  color: $color-black-light;

  @include viewport--md {
    padding: 0 0 0 5px;

    font-size: 21px;
    line-height: 37px;
  }
}

.modal__contacts svg {
  max-width: 20px;
  margin: 0 15px 0 0;

  fill: $color-black-light;

  @include viewport--md {
    max-width: 16px;
    margin: 0 10px 0 0;
  }
}

.modal__contacts + .btn {
  min-width: 352px;

  @include viewport--sm {
    min-width: 100%;
  }
}

.modal__contacts + .btn svg {
  max-width: none;

  @include viewport--md {
    max-width: 16px;
  }
}
