.form-input {
  position: relative;
  flex-direction: column;

  & input {
    @include input-style;
    @include errorMessage;

    &[data-input-autocomplete] {
      padding-right: 37px;

      & ~ svg {
        position: absolute;
        top: 31px;
        right: 17px;

        fill: $color-periwinkle;

        transition: transform $transition;
      }
    }

    &[data-input-autocomplete="on"] {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      border-bottom: none;
      box-shadow: none;

      & ~ svg {
        transform: rotate(180deg);
      }

      & ~ .autocomplete {
        position: absolute;
        top: 100%;
        left: 0;
        z-index: 100;

        width: 100%;
        height: auto;

        box-shadow: $shadow-light;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;

        & .autocomplete__option {
          @include input-box;

          border-radius: 0;
          background-color: $color-default-white;

          transition: background-color $transition;

          &:not(:last-of-type) {
            border-bottom: none;
          }

          &:last-of-type {
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }

          &:hover {
            cursor: pointer;
            background-color: $color-periwinkle;
          }

          &--active {
            background-color: $color-periwinkle;
          }
        }
      }
    }
  }

  & label {
    display: none;
  }

  &:not(:last-of-type) {
    margin: 0 0 16px;
  }

  & input[type="checkbox"] {
    display: none;

    &:checked ~ label {
      @include checkmark-style(top, -1px, left, 4px, 16, 12, $color-default-white-svg);

      &::before {
        background: $button-color;
        border: none;
      }
    }

    & ~ label {
      position: relative;

      display: flex;
      width: fit-content;

      font-size: 18px;
      line-height: 27px;
      color: $color-violet;

      transition: opacity $transition;

      user-select: none;

      @include checkmark-style(top, -1px, left, 4px, 16, 12, $color-periwinkle-svg);

      &:focus {
        outline: none;
      }

      &::before {
        content: "";

        display: block;
        width: 24px;
        height: 24px;
        margin-right: 13px;

        border: 1px solid $color-periwinkle;
        box-sizing: border-box;
        box-shadow: $shadow-periwinkle-25;
        border-radius: 4px;
      }

      &:hover {
        opacity: $opacity-hover;
        user-select: none;
        cursor: pointer;
      }
    }
  }

  & textarea {
    @include errorMessage;
  }

  &--private-house,
  &--nonstandard-address {
    display: flex;
  }
}
