@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin viewport--xl {
  @media (max-width: $viewport--xxl - 1) {
    @content;
  }
}

@mixin viewport--lg {
  @media (max-width: $viewport--xl - 1) {
    @content;
  }
}

@mixin viewport--md {
  @media (max-width: $viewport--lg - 1) {
    @content;
  }
}

@mixin viewport--sm {
  @media (max-width: $viewport--md - 1) {
    @content;
  }
}

@mixin list-style-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin input-style-reset {
  padding: 0;

  border: none;

  &:focus {
    outline: none;
  }
}

@mixin button-style-reset {
  padding: 0;

  font-size: 0;
  color: inherit;

  background: none;
  outline: inherit;
  border: none;

  cursor: pointer;
}

@mixin price {
  font-family: $font-family--second;
  font-size: 40px;
  font-weight: 800;
  line-height: 48px;

  @include viewport--md {
    font-size: 21px;
    line-height: 32px;
  }
}

@mixin price-block {
  .price-block__item {
    @include price;
    color: $color-violet;
    margin-right: 19px;

    @include viewport--md {
      font-size: 24px;
    }

    &--old {
      font-size: 27px;
      text-decoration: line-through;
      color: $color-orange;
      letter-spacing: -0.3px;

      @include viewport--md {
        font-size: 20px;
      }
    }

    @include viewport--md {
      margin-right: 10px;
    }
  }
}

@mixin on-hover {
  @media (min-width: $viewport--lg) {
    transition: opacity $transition;

    &:not(.btn):hover {
      cursor: pointer;
      opacity: $opacity-hover;
    }
  }
}

@mixin center-xy-by-flex-row {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin center-y-by-flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin h2 {
  margin: 0;
  font-family: $font-family--second;
  font-weight: 800;
  font-size: 48px;
  line-height: 62px;
  letter-spacing: -1px;
}

@mixin input-box {
  width: 100%;
  padding: 23px 17px 20px;

  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  letter-spacing: -0.2px;
  color: $color-black-dark;

  border: 1px solid $color-periwinkle;
  border-radius: 10px;

  @include viewport--md {
    padding: 18px 17px 17px;
  }
}

@mixin input-style {
  @include input-style-reset;
  @include input-box;

  box-shadow: $shadow-light;

  &::placeholder {
    color: $color-black-light;
    font-weight: normal;
    letter-spacing: -0.2px;
  }

  &:focus {
    padding: 32px 17px 11px;

    &::placeholder {
      color: transparent;
    }

    & + label {
      position: absolute;
      top: 13px;
      left: 18px;

      display: inline;

      font-size: 14px;
      line-height: 19px;
      letter-spacing: -0.2px;

      @include viewport--md {
        top: 9px;
      }
    }

    @include viewport--md {
      padding: 28px 17px 7px;
    }
  }
}

@mixin visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin checkmark-style($y-position, $y-val, $x-position, $x-val, $width, $height, $color) {
  &::after {
    content: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='%23#{$color}' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.122 1.103L6.203 8.02 2.878 4.694a1.354 1.354 0 00-1.919 0 1.354 1.354 0 000 1.919l4.807 4.806c.24.24.634.24.878 0l8.397-8.397a1.354 1.354 0 000-1.919 1.354 1.354 0 00-1.92 0z' fill='%23#{$color}' /%3E%3C/svg%3E");

    position: absolute;

    #{$y-position}: $y-val;
    #{$x-position}: $x-val;

    width: #{$width}px;
    height: #{$height}px;

    transition: opacity $transition;
  }
}

@mixin errorMessage {
  &[data-error] {
    border-color: $color-flamingo;
  }

  & ~ span[data-error-message] {
    padding: 3px 0 0 17px;

    font-size: 14px;
    line-height: 19px;
    color: $color-flamingo;
  }
}
